import axios from 'axios'
import ElementUI from "element-ui";

const request = axios.create({
  baseURL: 'http://localhost:7777',
  timeout: 5000
})

// request 拦截器
// 可以自请求发送前对请求做一些处理
// 比如统一加token，对请求参数统一加密
request.interceptors.request.use(config => {
  config.headers['Content-Type'] = 'application/json;charset=utf-8';

  let user = localStorage.getItem("userInfo") ? JSON.parse(localStorage.getItem("userInfo")) : null
  let token = localStorage.getItem("_t") ? localStorage.getItem("_t") : null
  if (user != null) {
    console.log(token);
    config.headers['token'] = token; // 设置请求头
  }
  return config
}, error => {
  return Promise.reject(error)
});

// response 拦截器
// 可以在接口响应后统一处理结果
request.interceptors.response.use(
  response => {
    let res = response.data;
    // 如果是返回的文件
    if (response.config.responseType === 'blob') {
      return res
    }
    // 兼容服务端返回的字符串数据
    if (typeof res === 'string') {
      res = res ? JSON.parse(res) : res
    }
    // 权限验证不通过给出提示
    if (res.code === 401) {
      ElementUI.Message({
        message: res.msg,
        type: 'error'
      });
      localStorage.removeItem("userInfo");
      localStorage.removeItem("_t");
      localStorage.removeItem("toOrderInfo");
      localStorage.removeItem("_oNum");
      // window.location.reload();
      this.$router.replace("/")
    }
    return res;
  },
  error => {
    console.log('err' + error) // for debug
    return Promise.reject(error)
  }
)

export default request